<template>
    <div class="box-contenido">
        <MenuVue></MenuVue>
        <div class="box-info">
            <h1>{{title}}</h1>
            <hr>
            <form>
                <label> Color 1: <input type="color" v-model="fcolor"></label>
                <label> Color 2: <input type="color" v-model="scolor"></label>
            </form>
            <br>
            <form  class="formGrandiente">
                <label class="labelinput"> <input type="text" class="value-css" :value="setColor"></label>
                <br>
                <label class="labelradio">
                    <input type="radio" value="1" v-model.number="orientation"> Right
                </label>
                <label class="labelradio">
                    <input type="radio" value="2" v-model.number="orientation"> Left
                </label>
                <label class="labelradio">
                    <input type="radio" value="3" v-model.number="orientation"> Top
                </label>
                <label class="labelradio">
                    <input type="radio" value="4" v-model.number="orientation"> Bottom
                </label>
            </form>
            <br>
            <div class="salida" :style="setColor"></div>
        </div>
    </div>
</template>


<script>
    
import MenuVue from "@/components/MenuVue.vue"

    export default {
        name:'gradiente',
        components:{
            MenuVue
        },
            data: () => ( {
            title:"Generador De Gradientes",
            fcolor:"#cccccc",
            scolor:"#d86886",
            orientation:1,
        }),
        computed: {
            setColor (){
                if (this.orientation == 1) {
                    return   `background: linear-gradient( to right, ${this.fcolor}, ${this.scolor});`
                } else if(this.orientation == 2) {
                    return   `background: linear-gradient( to left, ${this.fcolor}, ${this.scolor});`
                } else if(this.orientation == 3) {
                    return   `background: linear-gradient( to top, ${this.fcolor}, ${this.scolor});`
                }else {
                    return   `background: linear-gradient( to bottom, ${this.fcolor}, ${this.scolor});`
                }
            }
        }
    }
</script>